<template>
  <div class="wrapper">
    <div id="select">
    <v-select
      v-model="selected"
      :items="selectorItems"
      @change="onchange"
      label="Rover name"
    ></v-select>
    <v-text-field :rules="rules" @change="check" label="Sol" v-model="sol"></v-text-field>
    </div>  
    <div id="checkboxes">
             <div  class="cameraCheckbox" v-for="camera in cameras" :key="camera">
      <v-checkbox
        @click="check"

        :disabled="isCameraDisabled(camera)"
        v-model="selectedCameras"
        color="primary"
        :label="camera"
        :value="camera"
      ></v-checkbox>
    </div>
    </div>

  </div>
</template>

<script>
export default {
  data: () => ({
    photos: [],
    sol: 1,
    selectedCameras: [],
    disabledCameras: [],
    selected: "",
      rules: [
        value => !!value || 'Required.',
        value => (value && value > 0) || 'Only positive values.',
      ],
    cameras: [
      "FHAZ",
      "RHAZ",
      "MAST",
      "CHEMCAM",
      "MAHLI",
      "MARDI",
      "NAVCAM",
      "PANCAM",
      "MINITES",
    ],
    rover: [
      {
        name: "Curiosity",
        cameras: [
          "FHAZ",
          "RHAZ",
          "MAST",
          "CHEMCAM",
          "MAHLI",
          "MARDI",
          "NAVCAM",
        ],
      },
      {
        name: "Opportunity",
        cameras: ["FHAZ", "RHAZ", "NAVCAM", "PANCAM", "MINITES"],
      },
      {
        name: "Spirit",
        cameras: ["FHAZ", "RHAZ", "NAVCAM", "PANCAM", "MINITES"],
      },
    ],
  }),
  methods: {
    onchange() {
      const rover = this.rover.filter((item) => {
        return item.name.includes(this.selected);
      });
      this.disabledCameras = this.cameras.filter(
        (x) => !rover[0].cameras.includes(x)
      );
      this.selectedCameras = this.selectedCameras.filter(
        (x) => !this.disabledCameras.includes(x)
      );
      this.check();
    },
    isCameraDisabled(camera) {
      return this.disabledCameras.includes(camera);
    },
    async check() {
      if (this.selected && this.sol && this.selectedCameras.length > 0) {
        this.photos = await this.$store.dispatch("getPhotos", {
          name: this.selected.toLowerCase(),
          sol: this.sol,
          selectedCameras: this.selectedCameras,
        });
      } else this.photos = [];
      this.$emit("photosUpdated", {
        photos: this.photos,
      });
    },
  },
  computed: {
    selectorItems() {
      return this.rover.map((a) => a.name);
    },
  },
};
</script>

<style scoped>
.wrapper {
  display: grid;
  grid-template-areas:
    "select"
    "checkboxes";
  grid-template-columns: 1fr;
  grid-template-rows: 0fr 1fr;
  margin: 0;
}

#select {
  grid-area: select;
  width: 30%;
}
#checkboxes {
  grid-area:checkboxes;
}

.cameraCheckbox {
  width: 20%;
  display: inline-grid;
}
</style>